import validation from "../utils/validation";
import AuthService from "../api/AuthService";
import { redirect } from "react-router-dom";
import useLocalStorage from "../utils/useLocalStorage";

export default async function loginAction({ request }) {
  let formData = await request.formData();
  formData = Object.fromEntries(formData);

  if (!validation.isValidLoginInputs(formData)) {
    alert("아이디혹은 비밀번호를 확인해주세요.");
    return;
  }

  try {
    const { data } = await AuthService.login(formData);
    useLocalStorage.set(import.meta.env.VITE_TOKEN_KEY_NAME, data.token);
    useLocalStorage.set("CHATTY_NICKNAME", data.username);

    if (data.username) {
      return redirect(`/${data.username}`);
    }
  } catch (err) {
    if (err.response.status === 400) {
      alert("아이디혹은 비밀번호를 확인해주세요.");
    }
  }
}
