import React, { useCallback } from "react";
import { useParams } from "react-router-dom";
import css from "./QuestionForm.module.css";
import Input from "../Input/Input/";
import Button from "../Button/Button";
import useInput from "../../hooks/useInput";
import useMutateQuestion from "../../hooks/useMutateQuestion";
import { useQuestionContext } from "../../context/QuestionContext";
import useDebounce from "../../hooks/useDebounce";
import { useModal } from "../../context/ModalContext";
import Toast from "../Toast/Toast";

// 로직들을 페이지 컴포넌트로 뺄지 고민
function QuestionForm() {
  const { username } = useParams();
  const [, dispatch] = useQuestionContext();
  const [, { open }] = useModal();
  const [input, onInput, onReset] = useInput({ content: "" });

  // onSubmit함수 만들기
  const onSubmit = useDebounce(() => {
    useMutateQuestion(dispatch, username, input.content);
    onReset();
    open(<Toast desc="질문을 전송하였습니다." />);
  });

  const test = (e) => {
    e.preventDefault();
    onSubmit();
  };

  return (
    <form onSubmit={test}>
      <div className={css.QuestionForm}>
        <div className={css.formField}>
          <Input
            name="content"
            value={input.content}
            onChange={onInput}
            role="question"
            required={true}
            maxLength="100"
            placeholder="익명 질문을 보내보세요!"
          />
          <Button type="submit" role="questionSubmit">
            전송
          </Button>
        </div>
      </div>
    </form>
  );
}

export default React.memo(QuestionForm);
