import React, { useMemo, useState } from "react";
import { useNavigate, useNavigation, Form } from "react-router-dom";
import Button from "../../components/Button/Button";
import Input from "../../components/Input/Input";
import css from "../Login/Login.module.css";
import useInput from "../../hooks/useInput";
import requestEmailCode from "../../api/Auth/requestEmailCode";
import validation from "../../utils/validation";

export default function SignUp() {
  const navigation = useNavigation();
  const [input, onInput] = useInput({
    username: "",
    email: "",
    password: "",
    password2: "",
    verification_code: "",
  });

  const isLoading = useMemo(
    () => navigation.state === "loading",
    [navigation.state]
  );

  const codeBtnDisable = !validation.isValidEmail(input.email);
  return (
    <main>
      <section className={css.loginPage}>
        <div className={css.circles}>
          <div className={css.circleBlack}></div>
          <div className={css.circleWhite}></div>
        </div>
        <Form method="post" id="signup-form">
          <div
            className={`${css.inputs} ${
              navigation.state === "loading" ? css["loadingInput"] : ""
            }`}
          >
            <Input
              type="text"
              name="username"
              value={input.username}
              onChange={onInput}
              placeholder="아이디"
              required={true}
            />
            <div className={css.emailForm}>
              <Input
                role="email"
                type="email"
                name="email"
                value={input.email}
                onChange={onInput}
                placeholder="이메일"
                required={true}
              />
              <Button
                type="button"
                role="emailAuth"
                onClick={() => requestEmailCode(input)}
                disabled={codeBtnDisable}
              >
                인증코드 전송
              </Button>
            </div>
            <Input
              type="verification_code"
              name="verification_code"
              value={input.verification_code}
              onChange={onInput}
              placeholder="이메일 인증코드"
              required={true}
            />
            <Input
              type="password"
              name="password"
              value={input.password}
              onChange={onInput}
              placeholder="비밀번호"
              required={true}
            />
            <Input
              type="password"
              name="password2"
              value={input.password2}
              onChange={onInput}
              placeholder="비밀번호 확인"
              required={true}
            />
          </div>
          <MemoizedButtons isLoading={isLoading} />
        </Form>
      </section>

      <div className={css.cheese}>
        <p>ⓒ Cheese Co.</p>
      </div>
    </main>
  );
}

const MemoizedButtons = React.memo(({ isLoading }) => {
  const navigate = useNavigate();

  return (
    <div className={css.btns}>
      <Button
        type="button"
        role="loginBack"
        onClick={() => {
          navigate(-1);
        }}
      >
        이전으로
      </Button>
      <Button
        type="submit"
        role="signup"
        className={css.loginBtn}
        disabled={isLoading}
      >
        {isLoading ? <div className={css.spinner} aria-hidden /> : "회원가입"}
      </Button>
    </div>
  );
});
