import QuestionService from "../api/QuestionService";

export default async function useMutateQuestion(
  dispatch,
  target_profile,
  content
) {
  if (content.length === 0) {
    return;
  }

  if (content.length > 100) {
    alert("Exceeded 100 characters.");
    return;
  }

  try {
    const { data } = await QuestionService.mutateQuestion({
      target_profile,
      content,
    });
    dispatch({ type: "POST_QUESTION", payload: data });
  } catch (err) {
    console.error(err.response);
  }
}
