import AuthService from "../AuthService";

export default async function requestEmailCode(input) {
  if (!input.username || !input.email) {
    alert("아이디와 이메일을 모두 입력해 주세요.");
    return;
  }
  try {
    const emailFormData = {
      username: input.username,
      email: input.email,
    };
    const res = await AuthService.verifyEmail(emailFormData);
    if (res.status === 200) {
      alert(res.data.info);
    }
  } catch (err) {
    console.log(err.response.data);
    alert(err.response.data.error);
  }
};
