import React, { useState, useCallback } from "react";
import { useLoaderData } from "react-router-dom";
import useInput from "../../hooks/useInput";
import updateMypage from "../../api/User/updateMypage";
import MypageView from "./MypageView";
import { useModal } from "../../context/ModalContext";
import CardModal from "../../components/Modal/CardModal/CardModal";
import useLogout from "../../hooks/useLogout";

export default function MyPage() {
  const loaderData = useLoaderData();
  const { username, profile_image, profile_message } = loaderData;
  const logoutRequest = useLogout();
  const [, { open, close }] = useModal();
  const [image, setImage] = useState(profile_image);
  const [thumbnail, setThumbnail] = useState(null);
  const [editable, setEditable] = useState(false);
  const [input, onInput] = useInput({
    username,
    profile_message,
  });

  const editMypage = (e) => {
    e.preventDefault();
    toggleEditable();

    if (!editable) {
      return;
    }

    if (
      image === profile_image &&
      username === input.username &&
      profile_message === input.profile_message
    ) {
      return;
    }

    const formData = new FormData();
    if (input.username) {
      formData.append("username", input.username);
    }
    if (input.profile_message) {
      formData.append("profile_message", input.profile_message);
    }
    if (image !== profile_image) {
      console.log("here", image);
      formData.append("profile_image", image);
    }
    updateMypage(formData);
  };

  const changeImg = (e) => {
    if (e.target.files[0]) {
      setImage(e.target.files[0]);
    }
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setThumbnail(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };

  const openLogoutModal = useCallback(() => {
    open(
      <CardModal
        role="confirm"
        title="로그아웃 하시겠습니까?"
        desc="로그아웃 이후에도 익명으로 질문을 남길수 있습니다."
        buttonText={{ cancel: "취소", confirm: "확인" }}
        onClose={close}
        onSuccess={logoutRequest}
      />
    );
  }, [open, close, logoutRequest]);

  const toggleEditable = useCallback(() => {
    setEditable((prev) => !prev);
  }, []);

  const props = {
    loaderData,
    editable,
    input,
    onInput,
    thumbnail,
    changeImg,
    editMypage,
    openLogoutModal,
  };

  return <MypageView {...props} />;
}
