import React, { useMemo, useCallback } from "react";
import { useNavigate, useNavigation, Form } from "react-router-dom";
import Button from "../../components/Button/Button";
import Input from "../../components/Input/Input";
import css from "./Login.module.css";
import useInput from "../../hooks/useInput";
import Logo from "../../components/Logo/Logo";
import Warning from "../../components/Warning/Warning";

export default function Login() {
  const navigate = useNavigate();
  const navigation = useNavigation();
  const [input, onInput] = useInput({ username: "", password: "" });

  let isLoading = useMemo(
    () => navigation.state === "loading",
    [navigation.state]
  );

  const goBack = useCallback(() => navigate(-1));

  return (
    <main>
      <section className={css.loginPage}>
        <Logo />
        <Form method="post" id="login-form">
          <div
            className={`${css.inputs} ${isLoading ? css["loadingInput"] : ""}`}
          >
            <Input
              name="username"
              value={input.username}
              required={true}
              onChange={onInput}
              placeholder="아이디"
            />
            <Input
              type="password"
              name="password"
              value={input.password}
              onChange={onInput}
              required={true}
              placeholder="비밀번호"
            />
            {/* <Warning>비밀번호가 일치하지 않습니다.</Warning> */}
          </div>
          <MemoizedButtons isLoading={isLoading} goBack={goBack} />
        </Form>
      </section>

      <div className={css.cheese}>
        <p>ⓒ Cheese Co.</p>
      </div>
    </main>
  );
}

const MemoizedButtons = React.memo(({ isLoading, goBack }) => {
  return (
    <div className={css.btns}>
      <Button type="button" role="loginBack" onClick={goBack}>
        이전으로
      </Button>
      <Button type="submit" role="login" disabled={isLoading}>
        {isLoading ? <div className={css.spinner} /> : "로그인"}
      </Button>
    </div>
  );
});
