const REGEX = {
  email: /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i,

};

const validation = {
  isValidLoginInputs(formData) {
    const { username, password } = formData;

    if (username !== "" && password !== "") {
      return true;
    }
    return false;
  },
  isValidSignUpInputs(formData) {
    const { username, email, password, password2, verification_code } = formData;
    console.log(formData);
    if (username === "") {
      alert("아이디를 확인해주세요.");
      return false;
    }
    if (email === "") {
      alert("이메일을 입력해주세요.");
      return false;
    }
    if (!REGEX.email.test(email)) {
      return false;
    }

    if (password === "") {
      alert("비밀번호를 확인해주세요");
      return false;
    }
    if (password !== password2) {
      alert("비밀번호가 일치하지 않습니다.")
      return false;
    }
    if (!verification_code) {
      alert("이메일 인증코드를 확인해주세요.");
      return false;
    }
    return true;
  },
  isValidEmail(email) {
    return REGEX.email.test(email);
  }
}

export default validation;