import UserService from "../UserService";
import handleUnAuthorized from "../../utils/handleUnAuthorized";
import useLocalStorage from "../../utils/useLocalStorage";

export default async function updateMypage(formData) {
  let cnt = 0;
  let username;
  for (var data of formData.entries()) {
    if (data[1]) {
      if (data[0] === 'username') {
        username = data[1];
      }
      cnt++;
    }
  }
  if (cnt === 0) {
    return;
  }
  try {
    const res = await UserService.updateMypage(formData);
    if (username) {
      useLocalStorage.set(import.meta.env.VITE_NICKNAME, username)
    }
  } catch (err) {
    console.error(err.response);
    if (err.status === 400) {
      alert(err.response.data);
    }
    if (err.status === 401) {
      handleUnAuthorized(err);
    }
  }
}