import { useRef } from "react";

export default function useDebounce(callback, delay = 200) {
  const timer = useRef(null);

  const debounce = () => {
    if (timer.current) {
      clearTimeout(timer.current);
    }
    timer.current = setTimeout(() => {
      callback();
      clearTimeout(timer.current);
    }, delay);
  };

  return debounce;
}
