import QuestionService from "../api/QuestionService";
import handleUnAuthorized from "../utils/handleUnAuthorized";

export default async function useReject(dispatch, pk) {
  try {
    const { data } = await QuestionService.mutateReject(pk);
    dispatch({ type: "POST_REJECT", payload: data });
  } catch (err) {
    if (err.response.status === 401) {
      handleUnAuthorized(err);
    }

    if (err.response.status === 500) {
      alert("서버 오류입니다. 새로고침 후 다시 시도해주세요.");
    }
  }
}
